import React, { useEffect, useState } from 'react';
import { getBranches, getCommits } from './api';
import Tree from 'react-d3-tree';

const CommitGraph = ({ username, repo, token }) => {
  const [data, setData] = useState(null);

  useEffect(() => {
    if (username && repo && token) {
      getBranches(username, repo, token)
        .then((response) => {
          const branches = response.data;
          for (const branch of branches) {
            getCommits(username, repo, branch.name, token)
              .then((response) => {
                const commitData = response.data.map(commit => ({
                  name: commit.sha.substring(0, 7),
                  children: [],
                }));

                for (let i = 0; i < commitData.length - 1; i++) {
                  commitData[i].children.push(commitData[i + 1]);
                }

                setData(prevData => {
                  if (prevData) {
                    prevData.children.push(commitData[0]);
                    return { ...prevData };
                  } else {
                    return commitData[0];
                  }
                });
              });
          }
        });
    }
  }, [username, repo, token]);

  return (
    <div style={{ width: '100%', height: '800px' }}>
      {data && (
        <Tree data={data} orientation="vertical" />
      )}
    </div>
  );
};

export default CommitGraph;
